.buton-up {
  @extend %button;

  position: fixed;
  bottom: 10px;
  right: 10px;
  // bottom: 50px;
  // right: 150px;

  padding: 10px;

  opacity: 0;
  transition: opacity 250ms linear;
  pointer-events: none;

  &.is-show {
    opacity: 1;
    pointer-events: visible;
  }
}

.buton-up__icon {
  fill: currentColor;
}
